import * as Yup from 'yup'
import axios from '../../axios'
import  React, {Component} from 'react';
import { Formik, Form, Field } from 'formik';
import { Button,LinearProgress,Container,Paper, Grid} from '@material-ui/core';
import { TextField} from 'formik-material-ui';
import { connect } from 'react-redux'
const mapStateToProps = (state) => ({
  Dept_ID: state.Dept_ID,
  collegeID: state.collegeID
});
class deptInfra extends Component{
 
  render(){
  return (

  <Container fluid  maxWidth='xl'>
  <Paper>
  <div style={{'padding' : '2rem', 'marginLeft': '4rem', 'marginRight': '2rem'}}>
  <h5 class="card-title">Department Infrastructure Details</h5>
   
  <Formik
      initialValues={{
        Equipment_Name: '',
        Quantity:'',
        Remarks:''
      }}
      validationSchema={Yup.object({
      Equipment_Name: Yup.string()
          .required("Equipment Name Required ")
          .min(2,'Min 2 Characters')
          .max(200,'Max 200 Characters'),
      Quantity: Yup.string()
          .required("Equipment Quantity Required "),
      })
    }
       onSubmit=  {(values, { setSubmitting, resetForm }) => {
            setTimeout(() => {
            setSubmitting(false); 
            axios
              .post(
                "api/Deptinfra/" +
                  this.props.Dept_ID +
                  "/" +
                  this.props.collegeID,
                JSON.stringify(values),
                {
                  headers: {
                    // Overwrite Axios's automatically set Content-Type
                    "Content-Type": "application/json",
                    Accept: "application/json",
                  },
                }
              )
              .then((res) => {
                if (res.status === 200) {
                  alert("Data Saved in Database :", res.data.flag);
                }
              })
              .catch(function (error) {
                alert("Error occured:" + error);
              });
              resetForm()
             this.setState({
                editorState: ''
                });
          }, 500);
        }   
    }
    >
      {({ submitForm,
       isSubmitting,
        values }) => (
        <Form>
    <Grid container spacing={2}>
      <Grid item lg={6} xs={12}>
          <Field
            required
            component={TextField}
            name="Equipment_Name"          
            type="text"
            label="Equipment Name "
            variant="outlined"
            fullWidth
            />
     </Grid>
     <Grid item  lg={3} xs={6}>
            <Field
            required
            component={TextField}
            name="Quantity"          
            type="text"
            label="Quantity "
            variant="outlined"
            fullWidth
            />
         </Grid>   
     <Grid item  lg={3} xs={6}>
            <Field
            component={TextField}
            name="Remarks"          
            type="text"
            label="Remarks If Any "
            variant="outlined"
            fullWidth
            />
         </Grid>     
          {isSubmitting && <LinearProgress />}
          <Button
            variant="contained"
            color="primary"
            disabled={isSubmitting}
            onClick={submitForm}
          >
            Submit
          </Button>
     </Grid> 
        </Form>  
      )}
    </Formik>
    </div> 
    </Paper>
    </Container>
  ); 
}}
export default connect(
  mapStateToProps,
)(deptInfra);
